import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Home from '../pages/index';

const Default = (props) =>{
    return (
        <div>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route render={() => <p>not found!.</p>} />
            </Switch>
        </div>
    );  
}

export default Default;